@import url("https://fonts.googleapis.com/css?family=Cabin:400,600");
*,
*:after,
*:before {
  box-sizing: border-box; }

* {
  margin: 0;
  padding: 0; }

body {
  font-family: "Cabin", sans-serif;
  letter-spacing: 0.02em;
  font-weight: 600; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

.logo {
  width: 71px;
  height: 66px;
  background: url("../../../../img/logo.svg") no-repeat;
  background-size: cover; }

.header {
  height: 100vh;
  padding: 13vh 10%;
  background: #383737 url("../../../../templates/headers/views/header1/img/bg-img.jpg") center;
  background-size: cover; }
  .header__menu {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start; }
    .header__menu-link {
      font-size: 16px;
      line-height: 24px;
      color: #fff; }
      .header__menu-link:not(:first-child) {
        margin-left: 40px; }
      @media (max-width: 650px) {
        .header__menu-link {
          display: none; } }
    .header__menu-navigation {
      position: relative;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center; }
      .header__menu-navigation.is-active .header__dropdown-menu {
        display: block; }
      .header__menu-navigation-btn {
        position: relative;
        width: 18px;
        height: 18px;
        margin-left: 40px;
        color: #FFF;
        cursor: pointer; }
        .header__menu-navigation-btn:hover {
          color: #ccc; }
        .header__menu-navigation-btn:after {
          top: 50%;
          left: 0;
          position: absolute;
          width: 18px;
          height: 2px;
          background: currentColor;
          content: "";
          transform: translateY(-50%);
          box-shadow: 0 6px 0 0 currentColor, 0 -6px 0 0 currentColor; }
        .header__menu-navigation-btn.create-close {
          position: absolute;
          top: 30px;
          right: 30px;
          z-index: 5; }
          .header__menu-navigation-btn.create-close:after {
            box-shadow: none;
            transform: translateY(-50%) rotate(45deg); }
          .header__menu-navigation-btn.create-close:before {
            top: 50%;
            left: 0;
            position: absolute;
            width: 18px;
            height: 2px;
            background: currentColor;
            content: "";
            transform: translateY(-50%) rotate(-45deg); }
      @media (max-width: 650px) {
        .header__menu-navigation {
          position: static; } }
  .header__dropdown-menu {
    top: 100%;
    right: 0;
    position: absolute;
    z-index: 3;
    display: none;
    width: 200px;
    margin-top: 5px;
    border: 2px solid #fff;
    border-radius: 6px; }
    .header__dropdown-menu-link {
      font-size: 16px;
      line-height: 35px;
      color: #fff;
      display: block;
      text-align: center;
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
      .header__dropdown-menu-link:hover {
        text-decoration: none;
        background-color: rgba(56, 55, 55, 0.5); }
      .header__dropdown-menu-link:first-child {
        border-top: none; }
      .header__dropdown-menu-link:last-child {
        border-bottom: none; }
      @media (max-width: 650px) {
        .header__dropdown-menu-link {
          line-height: 70px; } }
    @media (max-width: 650px) {
      .header__dropdown-menu {
        top: 0;
        left: 0;
        width: auto;
        margin-top: 0;
        background-color: rgba(0, 0, 0, 0.8); } }
  .header__text {
    width: 90%;
    margin-top: 37vh; }
    @media (max-width: 650px) {
      .header__text {
